import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import docImage from '../../assets/images/aboutDoctor.png'
import { Form } from 'react-bootstrap'
import axios from 'axios'
import { message } from 'antd'
import { useDispatch } from 'react-redux'
import { setUser } from '../../features/userSlice'

const Login = () => {
  const [uniqueId, setUniqueId] = useState('')
  const [password, setPassword] = useState('')

  const [validated, setValidated] = useState(false)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const handleSubmit = async (e) => {
    try {
      const form = e.currentTarget
      if (form.checkValidity() === false) {
        e.preventDefault()
        e.stopPropagation()
        setValidated(true)

        return
      }

      e.preventDefault()
      e.stopPropagation()
      const formData = { uniqueId, password }

      const res = await axios.post('/api/v1/user/login', formData)
      if (res.data.success) {
        localStorage.setItem('token', res.data.token)
        message.success('Login Successfully')
        dispatch(setUser(res.data.data))
        if (res.data.data.role === 'admin') {
          navigate('/admin')
        }
        if (res.data.data.role === 'doctor') {
          navigate('/doctor')
        }
        if (res.data.data.role === 'receptionist') {
          navigate('/receptionist')
        }
        if (res.data.data.role === 'patient') {
          navigate('/patient')
        }
      } else {
        message.error(res.data.message)
      }
    } catch (error) {
      console.log(error)
      message.error('something went wrong')
    }
  }

  return (
    <>
      <section className="vh-100 d-flex align-items-center justify-content-center flex-column">
        <div className="container h-custom">
          <div className="row d-flex justify-content-center align-items-center h-100 shadow-lg rounded py-5">
            <div className="col-md-9 col-lg-6 col-xl-5">
              <img src={docImage} className="img-fluid" alt="" />
            </div>
            <div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
              <Form noValidate validated={validated} onSubmit={handleSubmit} autoComplete="off">
                <div className="form-outline mb-4">
                  <Form.Group controlId="validationCustom01">
                    <Form.Label>Unique Id</Form.Label>
                    <Form.Control
                      required
                      type="text"
                      placeholder="Unique Id"
                      className="form-control"
                      value={uniqueId}
                      onChange={(e) => setUniqueId(e.target.value)}
                      name="uniqueId"
                    />
                    <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                  </Form.Group>
                </div>
                <div className="form-outline mb-3">
                  <Form.Group controlId="validationCustom01">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      required
                      type="text"
                      placeholder="Password"
                      className="form-control"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      name="password"
                    />
                    <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                  </Form.Group>
                </div>
                <div className="d-flex justify-content-between align-items-center">
                  <a href="#!" className="text-body">
                    Forgot password?
                  </a>
                </div>
                <div className="text-center text-lg-start mt-4 pt-2">
                  <button type="submit" className="btn btn-primary btn-block mb-4">
                    Login
                  </button>
                  <div className="text-center">
                    <p className="small fw-bold mt-2 pt-1 mb-0">
                      Don&aps;t have an account?{' '}
                      <Link to="/register" className="link-danger">
                        Register
                      </Link>
                    </p>
                  </div>
                </div>
              </Form>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}
export default Login
